@import '_variables';
$color1: #f4f4f4;
$color2: #3197EE;

.post__property{
  position: relative;
  clear: both;
  display: block;
  width: 100%;
  float: left;
  margin-top: 0;
  padding: 60px 0;
  min-height: 100vh;
  h1{margin-bottom: 17px;}
  h4{margin-bottom: 27px;}
  .select__single{height: 50px;
    border-radius: 4px;
    background-color: $select;}
  .mr__27{margin-bottom: 27px;}
.post__radios {
  margin: 0.5rem;
  li{display: inline;float: left;}
  li:first-child {
      margin-left: 0;
  }
  label{margin-right: 17px;}
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    + .radio-label {
      &:before {
        content: '';
        background: $color1;
        border-radius: 100%;
        border: 1px solid darken($color1, 25%);
        display: inline-block;
        width: 1.4em;
        height: 1.4em;
        position: relative;
        top: 0px;
        margin-right: 5px; 
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &:checked {
      + .radio-label {
        &:before {
          background-color: $color2;
          box-shadow: inset 0 0 0 4px $color1;
        }
      }
    }
    &:focus {
      + .radio-label {
        &:before {
          outline: none;
          border-color: $color2;
        }
      }
    }
   
  }
}
}
.dropzone-wrapper {
  border: 2px dashed #91b0b3;
  color: #92b0b3;
  position: relative;
  height: 177px;
  margin-bottom: 37px;
  .dropzone-desc {
    position: absolute;
    margin: 0 auto;
    left: 0;
    right: 0;
    text-align: center;
    width: 47%;
    top: 50px;
    font-size: 16px;
    svg {font-size: 37px;}
     .adverts-form-required2 {
      float: none;
      color: red!important;
    }
  }
  input {
    margin-bottom: 27px;
    background: #f5f6f9;
    padding: 17px;
    position: absolute;
    outline: none!important;
    width: 100%;
    height: 150px;
    cursor: pointer;
    opacity: 0;
  }
  
}
.previewimg{
  width:77px;
