}

.rmsc {
  --rmsc-main: #ec0dda;
  --rmsc-hover: #f1f3f5;
  --rmsc-selected: #e2e6ea;
  --rmsc-border: #ccc;
  --rmsc-gray: #aaa;
  --rmsc-bg: #fff;
  --rmsc-p: 10px; /* Spacing */
  --rmsc-radius: 4px; /* Radius */
  --rmsc-h: 38px; /* Height */
}
.rmsc .options {
  position: relative;
  width:100%;
  float: left;
  word-break: break-all;
}
.rmsc .options li {
  input{
  background-color: #3197EE;
  width:auto;
  }
}
.rmsc .select-item {
  input{
    background-color: #3197EE;
    width:auto;
    }
}
.Rentsss{
  position: relative;
  clear: both;
